          "28- Derinin rengini gösterecek şekilde ince olan bir elbise ile avret yeri örtülmüş sayılmaz. Bunun için böyle bir elbise ile namaz sahih olmaz. Elbisenin darlığından dolayı avret yerinin belli olması, kötü bir hal ise de, namazın sıhhatine engel olmaz.",

          "29- Elbise bulacağını ümit eden çıplak bir kimse, vaktin çıkmasından korkmadıkça, bekler. Temiz yer bulacağını ümit eden kimse de, böyle yapar.",

          "30- Avret yerini örtecek bir şey bulamayan kimse, oturarak ve ayaklarını kıbleye doğru uzatarak imâ (işaret) ile namaz kılar. Onun için en iyi kılış şekli budur; çünkü bu vaziyette örtünme haline daha çok bürünmüş olur. Avret yerinin bir kısmını örtecek bir şey bulununca, onu kullanma vacib olur. Bu durumda önce avret-i galize denilen ön ve arka taraflar örtülür. Sonra erkeklerde butlar, daha sonra dizler örtülür. Kadınlarda butlardan sonra karınlar, sonra sırtlar ve dizler, daha sonra da geri kalan kısımlar örtülür.Bütün bunlar, namazın her halde yerine getirilmesini ve dinde çok önemli bir farz olduğunu göstermektedir.",

