          " 71- Sayd (av), yaratılışında vahşi olup insandan kaçınan, eti yensin yenmesin, herhangi bir hayvandır ki, ele geçirilmesi ancak bir hile ile mümkün olabilir. Böyle bir av hayvanını kaçamaz bir hale getirip elde etmeye İstiyad (Avlamak) denir.",

          "72- Bir av hayvanına karşı bir köpeğini salıvermeye “İrsal”, kışkırtıp sıçratmaya da “İğra” denir. İğra irsalden sonra olur.Ava kendiliğinden varan öğretilmiş köpek gibi bir hayvanın arkasından yapılacak “İğra”, irsal hükmündedir.",

          "73- Vahşi hayvanları avlamak caizdir. Bu mubah olan bir kazanç yoludur. Fakat diğer kazanç yolları bundan daha faziletlidir. Zevk ve eğlence için av avlamak uygun değildir. Kalbe katılık ve gaflet getirir. Yaratıklara karşı şefkat duygularını azaltır.",

          "Bununla beraber Yüce Allah bu hayvanları insanlar için yaratmıştır. İnsanlar ya bir ihtiyaç veya kendi cesaretlerini denemek veya bir genişlik bulmak için bazı hayvanları avlayabilirler. Çok kez yenecek hayvanları yenmek için, yenmeyecek hayvanlar da derileri, dişleri veya zararlarını kaldırmak için avlanır.Ancak bunları avlamak için başkalarının ekinlerini yok etmek, onları evlerinde rahatsız etmek caiz değildir."
