          " 77- Bir av etinin yenilebilmesi için şu şartlar gereklidir:",

          "1) Av, dinimizce eti yenen hayvanlardan olmalıdır.",

          "2) Avcı, hayvan boğazlamaya ehil bir müslüman veya kitab ehlinden olmalıdır. Bunlardan Besmeleyi bilen ve av kasdinde bulunan bir çocuğun veya delinin veya bir sarhoşun avladığı av helaldir. Fakat hac ve umre için ihramda bulunan bir müslümanın Harem Bölgesi dahilinde ve haricinde avlayacağı av helal olmaz.Yine, bir Mecûsî, putperest veya mürtedin (hak dinden çıkmışın) avladığı hayvanın eti de haramdır, bunlar yenmez.",

          "3) Avcı ava silah atarken veya hayvanı salıverirken gerçekten veya hükmen Besmele çekmiş olmalıdır. Besmeleyi unuttuğundan dolayı terk eden bir avcı, hükmen besmele getirmiş olur. Besmele kasden terk edilmiş olursa, avın eti yenmez, haramdır.(İmam Şafiîye göre, Besmele getirmek şart değildir, fakat bunu terk mekruhtur.)",

          "4) Avlanan hayvan, avcının henüz eline geçmeden almış olduğu yaradan dolayı ölmüş olmalıdır. Onun için henüz ölmeden ele geçirilirse, boğazlanması gerekir. Bu durumda boğazlanmadan ölürse, eti yenmez.",

          "5) Avcı silahı ile vurduğu veya öğretilmiş av hayvanı ile tutturup yaraladığı avı durdurmaksızın ele geçirmek için peşine koşmalıdır. Çünkü bu durumda avı daha ölmeden yakalayıp boğazlaması mümkündür. Bu boğazlama mümkün olukça hükmen boğazlama yeterli olmaz. Onun için bir süre durduktan sonra veya başka bir şeyle uğraşıp av gözden kaybolduktan sonra gidip de avı ölmüş bulsa, onun eti yenmez. Çünkü bu halde başka bir sebeble ölmüş olması, düşünülebilir. Fakat böyle beklemeksizin hemen gidip de avı yaralı olarak ölmüş bulsa, eti yenebilir. Buna göre hükmen bir boğazlama bulunmuş olur.",

          "6) Ava saldıran öğretilmiş hayvan da, bir süre durmayıp hemen ava doğru yürümelidir. Buna öğretilmemiş başka bir hayvan da eşlik etmelidir.Pars gibi öğretilmiş bir hayvanın, salıverildikten sonra dinlenmek için değil de, avını avlamak için hile olarak bir yere saklanıp duruvermesi, zarar vermez.",

          "7) Av köpekleri gibi öğretilmiş azı dişli av hayvanları, tuttukları avların etinden kendileri hiç yememelidir. Eğer bu avcı hayvanlar, tuttukları avları parçalayıp etlerinden yiyecek olurlarsa, artık avlanan hayvanların etleri yenmez. Fakat tırnaklı olan öğretilmiş hayvanların tutup etlerinden yedikleri hayvanlar, insanlar tarafından da yenir. Çünkü bu ikinci kısım hayvanların öğretilmiş olmaları, yemeyi terk suretiyle değildir; çağrıldıkları zaman geriye dönüp gelmeleri iledir."
